//urlPrefix
var urlPrefix = '';
if ($('#active-language').length) {
    urlPrefix = "/" + $('#active-language').val();
}

//show spin loading
jQuery.fn.extend({
    showLoading: function() {
        var over = '<div id="overlay"><i class="fa fa-spinner fa-pulse"></i></div>';
        this.css({position: "relative"});
        jQuery(over).appendTo(this);
    },
    hideLoading: function() {
        this.find('#overlay').remove();
    }
});

//ajax  
$(".form_ajax").each(function(e){
    var url = $(this).data('url');
    var renew = $(this).data('renew');
    var modal_content = $(this).parents('.form-content');
    var first = $(this).parents('.form-content').find('.form-content-first');
    var last = $(this).parents('.form-content').find('.form-content-last');

    $(this).validate({
        ignore: ".ignore",
        submitHandler: function (form) {
            //var formData = new FormData();
            //formData.append( 'file_attach', $('input[name=file]')[0].files[0]);

            var formData = new FormData($(form)[0]); //   ajax

            $.ajax({
                url: url,
                type: 'POST',
                async: true,
                cache: false,
                contentType: false,
                processData: false,
                //data: jQuery(form).serialize() ,
                data: formData,
                beforeSend: function(data) {
                    modal_content.showLoading();
                },
                complete: function(data) {
                    modal_content.hideLoading();
                    first.slideUp(600);
                    last.slideDown(600);

                    if(renew == 1) {
                        setTimeout(function () {
                            $("#add_more_file_container").show(); //  
                            first.slideDown(600);
                            last.slideUp(600);
                            jQuery(':input',modal_content).not(':button, :submit, :reset, :hidden').val('').removeAttr('checked').removeAttr('selected');
                        }, 4000);
                    }
                }
            });
        }
    });
});

//
$(document).ready(function() {
    $('.form_validate').each(function () {
        $(this).validate({
            ignore: ".ignore",
            highlight: function(element) {
                $(element).closest('.form-group').addClass('has-danger');
            },
            unhighlight: function(element) {
                $(element).closest('.form-group').removeClass('has-danger');
            },
        });
    });
});

/////////////BOOTSTRAP TABS/////////////////
$(function () {
    if ($('.nav-tabs').length){
        // show active tab on reload
        if (location.hash !== '') {
            $('a[href="' + location.hash + '"]').tab('show');
        }
        else {
            //show first tab
            $(".nav-tabs >li:first-child").addClass("active");
            $(".tab-content >div:first-child").addClass("active");
        }

        // remember the hash in the URL without jumping
        $('a[data-toggle="tab"]').on('shown.bs.tab', function(e) {
            if(history.pushState) {
                history.pushState(null, null, '#'+$(e.target).attr('href').substr(1));
            } else {
                location.hash = '#'+$(e.target).attr('href').substr(1);
            }
        });
    }
});

//    
$(document).ready(function() {
    if ($('.b-add-more-files, .b-add-more-images').length){
        $('.b-add-more-files, .b-add-more-images').each(function () {
            var max_fields      = 10; //maximum input boxes allowed
            var wrapper         = $(this).find("#input_fields_wrap"); //Fields wrapper
            var add_button      = $(this).find("#add_field_button"); //Add button ID
            var name            = $(this).data("name"); //name
            var x = 1; //initlal text box count

            if(!name) {
                if($(this).hasClass('b-add-more-files')) name = 'files';
                if($(this).hasClass('b-add-more-images')) name = 'images';
                console.log('ADD NAME: <div class="b-add-more-files/images" data-name="files">');
            }

            $(add_button).click(function(e){ //on add input button click
                e.preventDefault();
                if(x < max_fields){ //max input box allowed
                    if(x == 1) $(wrapper).append('<div><input type="file" name="'+name+'[]"/></div>');
                    else $(wrapper).append('<div><input type="file" name="'+name+'[]"/><button type="button" class="remove_button btn btn-danger btn-xs">remove</button></div>'); //add input box

                    x++; //text box increment
                }
            });

            $(wrapper).on("click",".remove_button", function(e){ //user click on remove text
                e.preventDefault(); $(this).parent('div').remove(); x--;
            });

            $(add_button).click();
        });
    }
});


